import Key from '../key/Key';
import './Keyboard.css';

function Keyboard(props) {
  return (
    <div className="Keyboard">
      <div className="Line">
        {["Q", "W", "E", "R", "T", "Y", "U", "I", "O", "P"].map((letter) => {
          return (
            <Key
              letter={letter}
              key={letter}
              handleClick={() => props.onKeyboardKeyPress(letter)} />)
        })}
      </div>
      <div className="Line">
        {["A", "S", "D", "F", "G", "H", "J", "K", "L"].map((letter) => {
          return (
            <Key
              letter={letter}
              key={letter}
              handleClick={() => props.onKeyboardKeyPress(letter)} />)
        })}
      </div>
      <div className="Line">
        {["", "Z", "X", "C", "V", "B", "N", "M", ""].map((letter) => {
          return (
            <Key
              letter={letter}
              key={letter}
              handleClick={() => props.onKeyboardKeyPress(letter)} />)
        })}
      </div>
    </div>
  );
}

export default Keyboard;
